* {
  box-sizing: border-box;
  scrollbar-color: lightgreen black;
  scrollbar-width: thin;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

body, .root, .App {
  width: 100vw;
  height: 100vh;
}

@keyframes bounceUp {
  0% {
    transform: scale(0);
  }

  25% {
    transform: scale(1.2);
  }

  50% {
    transform: scale(.9);
  }

  75% {
    transform: scale(1.05);
  }

  100% {
    transform: scale(1);
  }
}

.App {
  width: 100%;
  background: #dc143c;
}

.App main {
  width: 100%;
  height: 100%;
  background: #0000fa80;
  grid-auto-columns: 50%;
  grid-auto-flow: column;
  display: grid;
}

.App main .right, .App main .left {
  max-height: 100vh;
  overflow-y: auto;
}

.App main .left {
  padding: 50px;
}

.App main .left .Book {
  width: 100%;
  height: 100%;
  background: purple;
  border: 0 solid #0000;
  grid-template-rows: 80% auto auto;
  transition: box-shadow .3s, transform .3s, border .4s;
  animation: bounceUp .5s;
  display: grid;
  box-shadow: -5px 5px #00000080, -10px 10px #00000080, -15px 15px #00000080;
}

.App main .left .Book .name, .App main .left .Book .povCharacters {
  color: #fff;
  height: -moz-fit-content;
  height: fit-content;
  padding: 5px;
  overflow: auto;
}

.App main .left .Book .name {
  padding-bottom: 0;
}

.App main .left .Book .povCharacters {
  grid-auto-columns: minmax(50%, fit-content);
  grid-template-rows: 50% 50%;
  grid-auto-flow: column;
  gap: 5px;
  display: grid;
  overflow: hidden;
}

.App main .left .Book .povCharacters .tag {
  white-space: nowrap;
  background: #0003;
  border-radius: .3rem;
  padding: 5px;
  font-size: .7rem;
}

.App main .left .Book:hover {
  cursor: pointer;
  border-width: 5px;
  border-color: #0003;
  transform: scale(1.05);
  box-shadow: -10px 10px #0003, -25px 25px #0003, -40px 40px #0003;
}

.App main .left .Book:hover .povCharacters {
  overflow: auto;
}

.App main .left .Book .cover {
  width: 100%;
  background: #dbdbdb;
}

.App main .left .Book .cover img {
  width: 100%;
  height: 100%;
  display: block;
}

.App main .right {
  background: #639;
}

.App main .right * {
  z-index: 100;
  max-width: 100%;
  overflow-x: hidden;
}

.App main .right .Books .search {
  width: 100%;
  padding: 10px;
}

.App main .right .Books .items {
  padding: 0 10px 10px;
}

.App main .right .Books .items .item {
  cursor: pointer;
  color: #fff;
  -webkit-user-select: none;
  user-select: none;
  background: #0003;
  margin-bottom: 5px;
  padding: 5px;
  font-weight: 200;
  transition: transform .15s;
  animation: bounceUp .5s;
}

.App main .right .Books .items .item:hover {
  transform: scale(1.05);
}

.formField {
  width: 100%;
  height: 2rem;
  color: #000;
  background: #fff;
  border: none;
  padding: .3rem;
  font-size: 1.4rem;
  font-weight: 100;
}

.formField:focus {
  border: none;
  outline: none;
}

.PageLoading {
  width: 100vw;
  height: 100vh;
  background: #161616;
  justify-content: center;
  align-items: center;
  display: flex;
}

.PageLoading .spinner {
  border: 20px solid #fff;
  border-left-color: #0000;
  border-radius: 100%;
  animation: spinner 1s linear infinite;
}

@keyframes spinner {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

